body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-image: url('https://i.pinimg.com/564x/01/23/8a/01238a9179fbf5fa2a33c6074344cad1.jpg'); /* Background image */
  background-size: cover; /* Cover the entire background */
  background-position: center; /* Center the image */
  color: #4E3B31; /* Dark brown for text */
  height: 100vh; /* Full viewport height */
}

.navbar {
  background-color: rgba(210, 180, 140, 0.9); /* Light brown color for navbar */
  padding: 15px 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Subtle shadow for depth */
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  color: #fff; /* White logo text */
  font-size: 28px;
  font-weight: bold;
  text-transform: uppercase; /* Uppercase letters for logo */
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 30px;
}

.nav-links li {
  display: inline;
}

.nav-links a {
  text-decoration: none;
  color: #7d4d17; /* White text for links */
  font-size: 18px;
  padding: 8px 15px; /* Added padding for clickable area */
  border-radius: 5px; /* Rounded corners */
  transition: background-color 0.3s, color 0.3s; /* Smooth transition */
}

.nav-links a:hover {
  background-color: #f5d4a0; /* Light beige background on hover */
  color: #4E3B31; /* Dark brown text on hover */
}

.home-section {
  text-align: center;
  padding: 60px 20px; /* More padding for larger area */
  margin: 20px auto; /* Centered margin */
  max-width: 800px; /* Max width for better readability */
}

.home-section h1 {
  font-size: 48px; /* Increased size for "Beauty Starts Here" */
  margin-bottom: 20px; /* Spacing below title */
  color: #1d1305; /* Lighter brown for better visibility */
}

.home-section p {
  font-size: 20px; /* Slightly larger text */
  line-height: 1.6; /* Improved line spacing */
  color: #1d1305; /* Light brown for improved visibility */
}

section {
  padding: 20px;
}

.about-section {
  text-align: center;
  padding: 60px 20px;
  margin: 20px auto;
  max-width: 800px;
}

.about-section h2 {
  font-size: 36px;
  margin-bottom: 20px;
  color: #1d1305;
}

.about-section p {
  font-size: 18px;
  line-height: 1.6;
  color: #1d1305;
}

.contact-section {
  margin: 20px 0;
  text-align: center; /* Center align text */
}

.contact-section h2 {
  color: #1d1305; /* Lighter brown for heading */
}

.contact-section p {
  color: #1d1305; /* Light brown for contact section paragraph */
  text-size-adjust: 20px;
}

form {
  display: flex;
  flex-direction: column;
  max-width: 400px; /* Limit form width */
  margin: 20px auto; /* Center the form */
  background-color: rgba(175, 136, 110, 0.9); /* Light nude background for form */
  padding: 20px; /* Padding inside form */
  border-radius: 10px; /* Rounded corners */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Subtle shadow */
}

label {
  margin: 10px 0 5px;
  font-weight: bold; /* Bold labels */
  color: #4E3B31; /* Dark brown for labels */
}

input, textarea {
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #C2BBA0; /* Light brown border */
  border-radius: 5px; /* Rounded corners */
  font-size: 16px; /* Font size for inputs */
}

input:focus, textarea:focus {
  border-color: #8C6A4E; /* Medium light brown border on focus */
  outline: none; /* Remove outline */
}

button {
  padding: 12px;
  background-color: #8C6A4E; /* Medium light brown */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px; /* Slightly larger button text */
  transition: background-color 0.3s; /* Smooth transition */
}

button:hover {
  background-color: #f5d4a0; /* Light beige on hover */
  color: #4E3B31; /* Dark brown text on hover */
}

.hidden {
  display: none; /* Hide elements with this class */
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
